import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import { BrowserRouter } from "react-router-dom";
import { FilterProvider } from "./context/FilterContext";

const Root = () => {
  return (
    <FilterProvider>
      <App />
    </FilterProvider>
  );
};

ReactDOM.render(
  <BrowserRouter>
    <Root />
  </BrowserRouter>,
  document.getElementById("root")
);
