import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import DriveFileMoveIcon from "@mui/icons-material/DriveFileMove";
import MenuIcon from "@mui/icons-material/Menu";
import NewspaperIcon from "@mui/icons-material/Newspaper";
import OnlinePredictionIcon from "@mui/icons-material/OnlinePrediction";
import Box from "@mui/material/Box";
import Divider from "@mui/material/Divider";
import MuiDrawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import { styled } from "@mui/material/styles";
import * as React from "react";
import { Link, Redirect, Route, Switch, useLocation } from "react-router-dom";
import routes from "../routes";
import Header from "./Header";
import { useEffect, useState } from "react";
import HistoryIcon from '@mui/icons-material/History';

const drawerWidth = 240;

const openedMixin = (theme) => ({
  width: drawerWidth,
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: "hidden",
});

const closedMixin = (theme) => ({
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: "hidden",
  width: `calc(${theme.spacing(7)} + 1px)`,
  [theme.breakpoints.up("sm")]: {
    width: `calc(${theme.spacing(8)} + 1px)`,
  },
});

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "flex-start",
  padding: theme.spacing(0, 1),
  zIndex: "9999",
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}));

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    ...openedMixin(theme),
    "& .MuiDrawer-paper": openedMixin(theme),
  }),
  ...(!open && {
    ...closedMixin(theme),
    "& .MuiDrawer-paper": closedMixin(theme),
  }),
}));

export default function Layout() {
  const [open, setOpen] = useState(false);
  const location = useLocation();

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  useEffect(() => {
    console.log(open);
  }, [open]);

  return (
    <Box sx={{ display: "flex", backgroundColor: "#f8f8f8" }}>
      <Header handleDrawerOpen={handleDrawerOpen} open={open} />
      <Drawer variant="permanent" open={open}>
        <DrawerHeader>
          <IconButton onClick={() => setOpen(!open)}>
            {!open ? <MenuIcon /> : <ChevronLeftIcon />}
          </IconButton>
        </DrawerHeader>
        <Divider />
        <List>
          <Link to="/summary">
            <ListItem disablePadding sx={{ display: "block" }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? "initial" : "center",
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : "auto",
                    justifyContent: "center",
                    color:
                      location.pathname === "/summary" ? "#EA8600" : "inherit",
                  }}
                >
                  <NewspaperIcon />
                </ListItemIcon>
                <ListItemText
                  primary="Summary"
                  sx={{
                    opacity: open ? 1 : 0,
                    "& span": {
                      fontSize: "18px",
                      fontWeight: 500,
                      color:
                        location.pathname === "/summary"
                          ? "#EA8600"
                          : "#404040",
                    },
                  }}
                />
              </ListItemButton>
            </ListItem>
          </Link>
          <Link to="/mb-forecasting">
            <ListItem disablePadding sx={{ display: "block" }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? "initial" : "center",
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : "auto",
                    justifyContent: "center",
                    color:
                      location.pathname === "/mb-forecasting"
                        ? "#EA8600"
                        : "inherit",
                  }}
                >
                  <OnlinePredictionIcon />
                </ListItemIcon>
                <ListItemText
                  primary="MB Forecasting"
                  sx={{
                    opacity: open ? 1 : 0,
                    "& span": {
                      fontSize: "18px",
                      fontWeight: 500,
                      color:
                        location.pathname === "/mb-forecasting"
                          ? "#EA8600"
                          : "#404040",
                    },
                  }}
                />
              </ListItemButton>
            </ListItem>
          </Link>
          <Link to="/sub-assembly">
            <ListItem disablePadding sx={{ display: "block" }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? "initial" : "center",
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : "auto",
                    justifyContent: "center",
                    color:
                      location.pathname === "/sub-assembly"
                        ? "#EA8600"
                        : "inherit",
                  }}
                >
                  <DriveFileMoveIcon />
                </ListItemIcon>
                <ListItemText
                  primary="Sub-Assembly"
                  sx={{
                    opacity: open ? 1 : 0,
                    "& span": {
                      fontSize: "18px",
                      fontWeight: 500,
                      color:
                        location.pathname === "/sub-assembly"
                          ? "#EA8600"
                          : "#404040",
                    },
                  }}
                />
              </ListItemButton>
            </ListItem>
          </Link>
          <Link to="/recent-scenarios">
            <ListItem disablePadding sx={{ display: "block" }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? "initial" : "center",
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : "auto",
                    justifyContent: "center",
                    color:
                      location.pathname === "/recent-scenarios"
                        ? "#EA8600"
                        : "inherit",
                  }}
                >
                  <HistoryIcon />
                </ListItemIcon>
                <ListItemText
                  primary="Recent Scenarios"
                  sx={{
                    opacity: open ? 1 : 0,
                    "& span": {
                      fontSize: "18px",
                      fontWeight: 500,
                      color:
                        location.pathname === "/recent-scenarios"
                          ? "#EA8600"
                          : "#404040",
                    },
                  }}
                />
              </ListItemButton>
            </ListItem>
          </Link>
        </List>
      </Drawer>
      <Box
        component="main"
        sx={{ flexGrow: 1, padding: "140px 20px 20px", minHeight: "100vh" }}
      >
        {/* <DrawerHeader /> */}
        <Switch>
          {routes.map((prop) => {
            return (
              <Route
                path={prop.path}
                component={prop.component}
                key={prop.id}
              />
            );
          })}
          <Redirect to="/summary" />
        </Switch>
      </Box>
    </Box>
  );
}
