import { lazy } from "react";

const MbForecasting = lazy(() => import("./pages/mb-forcasting/MbForecasting"));
const ScenarioPlanner = lazy(() =>
  import("./pages/scenario-planner/ScenarioPlanner")
);
const SubAssembly = lazy(() => import("./pages/sub-assembly/SubAssembly"));
const Summary = lazy(() => import("./pages/summary/Summary"));
const Help = lazy(() => import("./pages/help/Help"));
const RecentScenarios = lazy(() => import("./pages/snap/RecentScenarios"));
const RecentScenariosFG = lazy(() => import("./pages/snap/RecentScenariosFG"));

const routes = [
  {
    path: "/summary",
    name: "summary",
    component: Summary,
    id: "summary",
  },
  {
    path: "/scenario-planner",
    name: "ScenarioPlanner",
    component: ScenarioPlanner,
    id: "ScenarioPlanner",
  },
  {
    path: "/mb-forecasting",
    name: "MbForecasting",
    component: MbForecasting,
    id: "MbForecasting",
  },
  {
    path: "/sub-assembly",
    name: "SubAssembly",
    component: SubAssembly,
    id: "SubAssembly",
  },
  {
    path: "/help",
    name: "Help",
    component: Help,
    id: "Help",
  },
  {
    path: "/recent-scenarios",
    name: "RecentScenarios",
    component: RecentScenarios,
    id: "RecentScenarios",
  },
  {
    path: "/recent-scenarios-fg",
    name: "RecentScenariosFG",
    component: RecentScenariosFG,
    id: "RecentScenariosFG",
  },
];

export default routes;
