import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import MuiAppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import IconButton from "@mui/material/IconButton";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import { styled } from "@mui/material/styles";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import React, { useContext, useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Logo from "../assets/PTLogo.png";
import Algo8Logo from "../assets/Algo8_logo.png";
import { Button, Stack, Modal } from "@mui/material";
import { FilterContext } from "../context/FilterContext";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import WarningIcon from "@mui/icons-material/Warning";
import { BaseUrl } from "../constant/BaseUrl";
import axios from "axios";

const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  marginLeft: "65px",
  width: `calc(100% - 65px)`,
  transition: theme.transitions.create(["width", "margin"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Header = ({ handleDrawerOpen, open }) => {
  const { filterState, setFilterState, handleLogout } =
    useContext(FilterContext);
  const [anchorEl, setAnchorEl] = useState(null);
  const isMenuOpen = Boolean(anchorEl);
  const handleProfileMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const getAllFilters = () => {
    axios
      .get(
        `${BaseUrl}${filterState?.location}/get-filter/${filterState.username}`
      )
      .then((res) => {
        // console.log(res.data);
        setFilterState({
          ...filterState,
          dateRange: res?.data[0]?.days || 15,
          productCode: res?.data[0]?.productCode || [""],
          filterName: res?.data[0]?.filterName || "filter",
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    getAllFilters();
  }, []);

  const handleSelectLocation = (location) => {
    axios
      .get(`${BaseUrl}${location}/get-filter/${filterState.username}`)
      .then((res) => {
        // console.log(res.data);
        setFilterState({
          ...filterState,
          dateRange: res?.data[0]?.days || 15,
          productCode: res?.data[0]?.productCode || [""],
          filterName: res?.data[0]?.filterName || "filter",
          location: location,
          selectedFG: "",
          PTNo: "",
        });
        localStorage.removeItem("PTNo");
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const location = {
    fontSize: "20px",
    color: "#404040",
    fontWeight: "600",
    cursor: "pointer",
    lineHeight: "31.27px",
  };

  const selectedLocation = {
    fontWeight: "600",
    fontSize: "20px",
    lineHeight: "31.27px",
    color: "#0077B6",
    cursor: "pointer",
    textDecoration: "underline",
  };

  const style = {
    position: "absolute",
    top: "30%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 550,
    bgcolor: "#fff",
    border: "none",
    boxShadow: "0px 3px 25px #00000080",
    borderRadius: "5px",
    padding: "20px",
    display: "flex",
    flexDirection: "column",
    gap: "10px",
  };
  const [openManualRefresh, setOpenManualRefresh] = useState(false);
  const [openResetData, setOpenResetData] = useState(false);

  const [dbLocations, setDbLocations] = useState({
    Amery: false,
    Protec: false,
    Mexico: false,
  });
  const onChangeDbLocation = (event, name) => {
    setDbLocations({
      ...dbLocations,
      [event.target.name]: event.target.checked,
    });
  };

  const [locationArray, setLocationArray] = useState([]);

  useEffect(() => {
    let myArray = [];

    for (let key in dbLocations) {
      if (dbLocations[key]) {
        myArray.push(key);
      } else {
        let index = myArray.indexOf(key);
        if (index !== -1) {
          myArray.splice(index, 1);
        }
      }
    }
    setLocationArray(myArray);
  }, [dbLocations]);

  const handleProceedManualrefresh = () => {
    console.log(locationArray);
  };
  const handleResetDatare = () => {
    axios
      .get(`${BaseUrl}${filterState?.location}/reset`)
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <>
      <CssBaseline />
      <AppBar
        position="fixed"
        open={open}
        color="inherit"
        sx={{
          backgroundColor: "#fff",
          boxShadow: "#0000000F 0px 3px 12px",
          zIndex: "99",
        }}
      >
        <Toolbar>
          <img src={Algo8Logo} alt="tech" width="auto" height="48px" />
          <Typography
            component="div"
            sx={{
              flex: 1,
              fontWeight: "600",
              fontSize: "24px",
              lineHeight: "31.27px",
            }}
          >
            <span style={{ color: "#0077B6" }}>Plant</span>
            <span style={{ color: "#EA8600" }}>Brain</span>
          </Typography>
          <Stack sx={{ alignItems: "center" }} direction="row" spacing={3}>
            {filterState?.locationArr.includes("amery") && (
              <div
                onClick={() => handleSelectLocation("amery")}
                style={
                  filterState?.location === "amery"
                    ? selectedLocation
                    : location
                }
              >
                Amery
              </div>
            )}
            {filterState?.locationArr.includes("protec") && (
              <div
                onClick={() => handleSelectLocation("protec")}
                style={
                  filterState?.location === "protec"
                    ? selectedLocation
                    : location
                }
              >
                Mentor
              </div>
            )}
            {filterState?.locationArr.includes("mexico") && (
              <div
                onClick={() => handleSelectLocation("mexico")}
                style={
                  filterState?.location === "mexico"
                    ? selectedLocation
                    : location
                }
              >
                Mexico
              </div>
            )}
          </Stack>
          <Box
            sx={{
              display: { xs: "none", md: "flex" },
              flex: 1,
              justifyContent: "flex-end",
            }}
          >
            <Typography
              component="div"
              sx={{
                display: "flex",
                alignItems: "center",
                marginRight: "20px",
                fontWeight: "500",
                fontSize: "18px",
                lineHeight: "24px",
              }}
            >
              {filterState?.username?.split("@")[0]}
            </Typography>
            <img src={Logo} alt="tech" width="auto" height="48px" />
            <IconButton
              className={isMenuOpen ? "usericon" : "nav-item"}
              size="large"
              aria-label="account of current user"
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
            >
              <AccountCircleIcon />
              {isMenuOpen ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
          </Box>
        </Toolbar>
        <Menu anchorEl={anchorEl} open={isMenuOpen} onClose={handleMenuClose}>
          <Link to="/help">
            <MenuItem onClick={() => handleMenuClose()} className="menu-link">
              Help
            </MenuItem>
          </Link>
          <Link to="/">
            <MenuItem
              className="menu-link"
              onClick={() => {
                handleLogout();
                handleMenuClose();
                localStorage.removeItem("isLoggedIn");
                localStorage.removeItem("user");
                localStorage.removeItem("PTNo");
                localStorage.removeItem("SelectedParent");
              }}
            >
              Sign out
            </MenuItem>
          </Link>
          <MenuItem>
            <Button
              onClick={() => {
                setOpenManualRefresh(true);
              }}
              variant="contained"
              sx={{
                textTransform: "none",
                p: "2px 15px",
                flex: 1,
                backgroundColor: "#0077B6",
                color: "#fff",
              }}
            >
              Manual Refresh
            </Button>
            <Modal
              open={openManualRefresh}
              onClose={() => {
                setOpenManualRefresh(false);
                handleMenuClose();
              }}
              aria-labelledby="modal-modal-title"
              aria-describedby="modal-modal-description"
            >
              <Box sx={style}>
                <div
                  style={{
                    fontSize: "24px",
                    lineHeight: "30px",
                    fontWeight: "600",
                    color: "#F0983D",
                    display: "flex",
                    alignItems: "center",
                    gap: "5px",
                  }}
                >
                  <WarningIcon fontSize="large" />
                  Warning!
                </div>
                <div style={{ display: "flex", alignItems: "center" }}>
                  <div style={{ fontSize: "15px", fontWeight: "500" }}>
                    Select DB to refresh :
                  </div>
                  <div
                    style={{
                      display: "flex",
                      flexGrow: 1,
                      justifyContent: "space-evenly",
                    }}
                  >
                    <FormGroup row>
                      <FormControlLabel
                        name="Amery"
                        checked={dbLocations?.Amery}
                        onChange={onChangeDbLocation}
                        control={<Checkbox size="small" />}
                        label="Amery"
                      />
                      <FormControlLabel
                        name="Protec"
                        checked={dbLocations?.Protec}
                        onChange={onChangeDbLocation}
                        control={<Checkbox size="small" />}
                        label="Mentor"
                      />
                      <FormControlLabel
                        name="Mexico"
                        checked={dbLocations?.Mexico}
                        onChange={onChangeDbLocation}
                        control={<Checkbox size="small" />}
                        label="Mexico"
                      />
                    </FormGroup>
                  </div>
                </div>
                <div style={{ fontSize: "15px", fontWeight: "500" }}>
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                  Recusandae illo reiciendis deleniti
                </div>
                <div
                  style={{
                    display: "flex",
                    justifyContent: "flex-end",
                    gap: "30px",
                    marginTop: "20px",
                  }}
                >
                  <Button
                    onClick={() => {
                      setOpenManualRefresh(false);
                      handleMenuClose();
                    }}
                    variant="contained"
                    sx={{
                      textTransform: "none",
                      p: "3px 40px",
                      backgroundColor: "#9F9F9F",
                      color: "#fff",
                    }}
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={() => {
                      setOpenManualRefresh(false);
                      handleProceedManualrefresh();
                      handleMenuClose();
                    }}
                    variant="contained"
                    disabled={
                      !dbLocations.Amery &&
                      !dbLocations.Mexico &&
                      !dbLocations.Protec
                    }
                    sx={{
                      textTransform: "none",
                      p: "3px 40px",
                      backgroundColor: "#0077B6",
                      color: "#fff",
                    }}
                  >
                    Proceed
                  </Button>
                </div>
              </Box>
            </Modal>
          </MenuItem>
          <MenuItem>
            <Button
              onClick={() => {
                setOpenResetData(true);
              }}
              variant="contained"
              sx={{
                textTransform: "none",
                p: "2px 3px",
                flex: 1,
                backgroundColor: "#0077B6",
                color: "#fff",
              }}
            >
              Reset Data
            </Button>
            <Modal
              open={openResetData}
              onClose={() => {
                setOpenResetData(false);
                handleMenuClose();
              }}
              aria-labelledby="modal-modal-title"
              aria-describedby="modal-modal-description"
            >
              <Box sx={style}>
                <div
                  style={{
                    fontSize: "24px",
                    lineHeight: "30px",
                    fontWeight: "600",
                    color: "#F0983D",
                    display: "flex",
                    alignItems: "center",
                    gap: "5px",
                  }}
                >
                  <WarningIcon fontSize="large" />
                  Warning!
                </div>
                <div style={{ fontSize: "15px", fontWeight: "500" }}>
                  This change is irreversible, Are you sure to RESET the Data
                </div>
                <div
                  style={{
                    display: "flex",
                    justifyContent: "flex-end",
                    gap: "30px",
                    marginTop: "20px",
                  }}
                >
                  <Button
                    onClick={() => {
                      setOpenResetData(false);
                      handleMenuClose();
                    }}
                    variant="contained"
                    sx={{
                      textTransform: "none",
                      p: "3px 40px",
                      backgroundColor: "#9F9F9F",
                      color: "#fff",
                    }}
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={() => {
                      setOpenResetData(false);
                      handleResetDatare();
                      handleMenuClose();
                    }}
                    variant="contained"
                    sx={{
                      textTransform: "none",
                      p: "3px 40px",
                      backgroundColor: "#0077B6",
                      color: "#fff",
                    }}
                  >
                    Proceed
                  </Button>
                </div>
              </Box>
            </Modal>
          </MenuItem>
        </Menu>
      </AppBar>
    </>
  );
};

export default Header;
