import { Visibility, VisibilityOff } from "@mui/icons-material";
import {
  FormControl,
  IconButton,
  InputAdornment,
  InputBase,
} from "@mui/material";
import { useFormik } from "formik";
import { useContext, useState } from "react";
import { useHistory } from "react-router";
import * as yup from "yup";
import { FilterContext } from "../context/FilterContext";
import axios from "axios";
import { BaseUrl } from "../constant/BaseUrl";

const validationSchema = yup.object({
  Email: yup
    .string("Enter your email")
    .required("Email is required")
    .email("Enter a valid email"),
  Password: yup
    .string("Enter your password")
    .matches(
      /^.*(?=.{8,})((?=.*[!@#$%^&*()\-_=+{};:,<.>]){1})(?=.*\d)((?=.*[a-z]){1})((?=.*[A-Z]){1}).*$/,
      "Password must contain at least 8 characters, one uppercase, one number and one special case character"
    )
    .required("Password is required"),
});

const SignIn = ({ setSignup, setForgotpass }) => {
  const { filterState, setFilterState } = useContext(FilterContext);
  const [showPassword, setShowPassword] = useState(false);
  const history = useHistory();

  const formik = useFormik({
    initialValues: {
      Email: "",
      Password: "",
    },

    validationSchema: validationSchema,
    onSubmit: (values) => {
      handleFormSubmit(values);
    },
  });

  const handleFormSubmit = async (val) => {
    axios
      .post(`${BaseUrl}login`, {
        username: val.Email,
        password: val.Password,
      })
      .then((res) => {
        console.log(res?.data);
        if (res?.data?.msg === "Logged In Successfully") {
          if (res?.data?.data?.userData?.location?.length > 0) {
            localStorage.setItem("isLoggedIn", true);
            setFilterState({
              ...filterState,
              loggedin: true,
              location: res?.data?.data?.userData?.location[0],
              orderBy: "ctb",
              rowsPerPage: 10,
              page: 0,
              username: val.Email,
              dateRange: res?.data?.data?.filterData[0]?.days || 15,
              productCode: res?.data?.data?.filterData[0]?.productCode || [""],
              filterName:
                res?.data?.data?.filterData[0]?.filterName || "filter",
              accessArr: res?.data?.data?.userData?.access || [],
              locationArr: res?.data?.data?.userData?.location || [],
            });
            history.push("/");
            alert(res?.data?.msg);
          } else {
            alert(
              "You dont't have any access, please ask administration to give you access to any of the location"
            );
          }
        } else {
          alert(res?.data?.msg);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      <div style={{ fontSize: "2.2rem", fontWeight: "normal" }}>Login</div>
      {/* <div
        style={{
          fontSize: '1rem',
          marginTop: '0.5rem',
          marginBottom: '0.5rem',
        }}
      >
        New user?{' '}
        <span
          style={{
            color: '#185ABC',
            cursor: 'pointer',
            textDecoration: 'underline',
          }}
          onClick={() => setSignup(true)}
        >
          Create an account
        </span>
      </div> */}
      <form onSubmit={formik.handleSubmit}>
        <div className="form-group">
          <FormControl fullWidth>
            <InputBase
              fullWidth
              type="text"
              className="input-border"
              placeholder="Email"
              name="Email"
              value={formik.values.Email}
              onChange={formik.handleChange}
              variant="standard"
            />
            {formik.touched.Email && Boolean(formik.errors.Email) ? (
              <p style={{ color: "red" }}>
                {formik.touched.Email && formik.errors.Email}
              </p>
            ) : null}
          </FormControl>
        </div>
        <div className="form-group">
          <FormControl fullWidth>
            <InputBase
              fullWidth
              type={showPassword ? "text" : "password"}
              placeholder="Password"
              name="Password"
              className="input-border"
              value={formik.values.Password}
              onChange={formik.handleChange}
              variant="standard"
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                    edge="end"
                  >
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              }
            />
            {formik.touched.Password && Boolean(formik.errors.Password) ? (
              <p style={{ color: "red" }}>
                {formik.touched.Password && formik.errors.Password}
              </p>
            ) : null}
          </FormControl>
        </div>
        {/* <div className='form-group'>
          <span
            style={{
              color: '#185ABC',
              cursor: 'pointer',
              display: 'flex',
              justifyContent: 'flex-end',
              textDecoration: 'underline',
            }}
            onClick={() => setForgotpass(true)}
          >
            Forgot Password?
          </span>
        </div> */}
        <div className="form-btn">
          <input type="submit" value="Login" className="btn" />
        </div>
      </form>
    </div>
  );
};

export default SignIn;
