import React, { createContext, useState } from "react";

const FilterContext = createContext(null);

const FilterProvider = ({ children }) => {
  const [filterState, setFilterState] = useState({
    loggedin: localStorage.getItem("isLoggedIn") || false,
    location: "",
    dateRange: "All",
    orderBy: "ctb",
    rowsPerPage: 10,
    page: 0,
    selectedFG: "",
    PTNo: "",
    username: "",
    productCode: [],
    filterName: "",
    snapSelectedFG: "",
    FGs: [],
    SelectedPTQOH: "",
    itemToScroll: "",
    accessArr: [],
    locationArr: [],
  });
  const handleLogout = () => {
    setFilterState({
      loggedin: false,
      location: "",
      dateRange: "",
      orderBy: "",
      rowsPerPage: null,
      page: 0,
      selectedFG: "",
      PTNo: "",
      username: "",
      productCode: [],
      filterName: "",
      snapSelectedFG: "",
      FGs: [],
      SelectedPTQOH: "",
      itemToScroll: "",
      accessArr: [],
      locationArr: [],
    });
  };
  return (
    <FilterContext.Provider
      value={{ filterState, setFilterState, handleLogout }}
    >
      {children}
    </FilterContext.Provider>
  );
};

export { FilterContext, FilterProvider };
